import { Component, OnInit } from '@angular/core';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-cash-payments-new',
  templateUrl: './cash-payments-new.component.html',
  styleUrls: ['./cash-payments-new.component.css']
})
export class CashPaymentsNewComponent implements OnInit {
  is_cash: any;
  is_cheque: any;
  internal_transfer: any;
  onSearch:any;
  otherdisable: boolean;
  // Eslip=5678910;
 
  checked : any=['is_cash','is_cheque','internal_transfer'];
  acc: boolean;
  validateCheque: boolean;
  chequeNO="";
  accNo="";
 
  constructor() { }

  ngOnInit() {
   
  }
  getValueForcash(event)
  {
    
    this.is_cash=event.checked;
    // console.log(event.checked);
    // this.is_cheque!=event.checked;

  

    
  }
  getcheque(event)
  {
    this.is_cheque=event.checked;
    // this.is_cash!=event.checked;
    // this.internal_transfer!=event.checked;
   
  }
  getInternalTansefer(event)
  {
    this.internal_transfer=event.checked;
    // this.is_cash!=event.checked;
    // this.is_cheque!=event.checked;
    
  }
  onSearchcall(prn)
  {console.log("prn",prn);

    if(prn!="5678910")
    {
      Swal.fire(
        {
          title:"Failed To Fetch The Details"
        }
      );
      this.onSearch=false;
    }
    else{
     this.onSearch=true;
    }
  }
  onPostofCash()
  {
    Swal.fire(
      {
        title:"Transaction Posted Successfully"
      }
    )
  }
  onPostcheque()
  {
    Swal.fire(
      {
        title:"Transaction Posted Successfully"
      }
    )
  }
  onValidateAccountNo(accNum)
  {
    if(!accNum)
    {
      Swal.fire({
        title:"Enter Account Number"
      })
    }
    else if(accNum=="123456789"){

    this.acc=true;
    this.validateCheque=false;
    this.chequeNO="";
    }
    else{
      Swal.fire({
        title:"Entered Account Number is not Valid"
      })
    }
  }
  CancelCash()
  {
    Swal.fire(
      {
        title:"Transaction Canceled" 
      }
    )
  }
  cancelCheque()
  {
    Swal.fire(
      {
        title:"Transaction Canceled" 
      }
    )
  }
  onvalidatecheque(chque)
  {
    if(!chque)
    {
      Swal.fire({
        title:"Please Enter Cheque Number"
      })
      this.acc=false;
    }
    else if(chque=="9876"){
    this.validateCheque=true;
    this.acc=false;
    }
    else{
      Swal.fire({
        title:"Entered cheque No is not Valid"
      })
    }
  }
  postCheque()
  {
    Swal.fire(
      {
        title:"Transaction Posted Successfully"
      }
    )
  }
}
