import { Component, OnInit } from '@angular/core';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-cash-payment-authorization',
  templateUrl: './cash-payment-authorization.component.html',
  styleUrls: ['./cash-payment-authorization.component.css']
})
export class CashPaymentAuthorizationComponent implements OnInit {
  serach: boolean;

  constructor() { }

  ngOnInit() {
  }
  onSearchCall(prn)
  {
    if(prn!="5678910")
    {
      this.serach=false;
    Swal.fire(
      {
        title:"Failed To Fetch the Deatils"
      }
     
    );
    }
    else{
    this.serach=true;
    }
  }
  onReject()
  {
    Swal.fire(
      {
        title:"Payment Rejected"
      }
    )
  }
  onRework()
  {
    Swal.fire(
      {
        title:"Payment For Rework"
      }
    )
  }
  onApprove()
  {
    Swal.fire(
      {
        title:"Payment Approved"
      }
    )
  }
}
